function dateTimevalidation(dateVal)
{
	var retval = true;
	var givenDate = new Date(Date.parse(dateVal));
	var currentDate = new Date(); //todays date to check if given date is in future

	if (givenDate=='Invalid Date') {
		retval = false;
	}
	//to check if date is in future if date is valid
	else if (givenDate> currentDate) {
		retval = false;
	}

	return retval;
}

module.exports = dateTimevalidation;